<template>
  <div class="searchareawrapper">
    <div class="maintext">
      <h2 v-if="value <= 1">
        <strong>{{ value }}</strong
        ><br />great track with {{ query }}
      </h2>
      <h2 v-else>
        <strong>{{ value }}</strong
        ><br />
        great tracks with {{ query }}
      </h2>
    </div>
    <div class="search-area">
      <input
        class="form-control form-control-lg"
        id="search-bar"
        placeholder="Type in artist or word"
        v-model="query"
        @keypress="fetchMusic"
      />
      <button
        type="button"
        class="btn btn-secondary btn-lg"
        :disabled="value === 1"
        @click="increase(-1)"
      >
        -
      </button>
      <button
        type="button"
        class="btn btn-secondary btn-lg"
        @click="increase(1)"
      >
        +
      </button>
      <button
        type="button"
        class="btn btn-secondary btn-lg"
        @click="searchMusic"
      >
        Search
      </button>
    </div>
    <section class="tracks-wrapper">
      <article class="all-tracks" v-for="tracks in artist" :key="tracks.id">
        <h1 class="track-title">{{ tracks.track.track_name }}</h1>
        <span class="track_artist">{{ tracks.track.artist_name }}</span> ~
        <span class="track-album"> {{ tracks.track.album_name }}</span>
        <button class="btn btn-secondary btn-sg" @click="addToPlaylist(tracks)">
          Add to playlist
        </button>
        <p v-if="5 < 8">
          {{ currentTracks }}
        </p>

        <p />
        <br />
        <br />
        <br />
      </article>
    </section>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'SearchArea',
    props: {
      alltracks: {
        type: Boolean,
        required: true
      }
    },
    data() {
      return {
        corsURL: 'https://cors-anywhere.herokuapp.com/',
        baseURL: 'https://api.musixmatch.com/ws/1.1/',
        query: '',
        format: 'json',
        value: 1,
        artist: [],
        currentTracks: '',
        songs: ''
      }
    },
    methods: {
      fetchMusic(e) {
        if (e.key === 'Enter') {
          axios
            .get(
              `https://api.musixmatch.com/ws/1.1/track.search?q_artist=${this.query}&page_size=${this.value}&track.lyrics.get?track_id=${this.query}&page=1&s_track_rating=desc&apikey=5018c9280bcd8207badfea78b0288e9e`,
              {
                headers: {
                  'Access-Control-Allow-Origin': '*'
                }
              }
            )
            .then((response) => response)
            .then(this.setResults)
        }
      },
      setResults(results) {
        this.artist = results.data.message.body.track_list
        console.log(this.artist)
        this.songs = results.data.message.body.track_list.track
      },
      increase(e) {
        this.value += e
      },
      searchMusic() {
        axios
          .get(
            `https://api.musixmatch.com/ws/1.1/track.search?q_artist=${this.query}&page_size=${this.value}&page=1&s_track_rating=desc&apikey=5018c9280bcd8207badfea78b0288e9e`,
            {
              headers: {
                'Access-Control-Allow-Origin': '*'
              }
            }
          )
          .then((response) => response)
          .then(this.setResults)
      },
      addToPlaylist(tracks) {
        if (sessionStorage.getItem('playlist')?.length) {
          const currentTracks = JSON.parse(sessionStorage.getItem('playlist'))
          console.log('saved tracks', currentTracks)
          const newData = [...currentTracks, tracks]
          sessionStorage.setItem('playlist', JSON.stringify(newData))
        } else {
          sessionStorage.setItem(
            'playlist',
            JSON.stringify([tracks.track_list])
          )
        }
      }
    },
    watch: {
      tracks: {
        handler() {
          sessionStorage.setItem('playlist', JSON.stringify(this.tracks))
        },
        deep: true
      }
    }
  }
</script>

